@use 'global' as *;

.section-title-sub {
    position: relative;
}
.section-title-sub::before,
.section-title-sub::after {
    position: absolute;
    content: "";
    top: 35%;
    transform: translate(-50%, -50%);
    background-color: $white;
    width: rem(280);
    height: rem(1);
    @include mq('ll') {
       width: rem(140); 
    }
    @include mq('md') {
        width: rem(140);
    }
}
.section-title-sub::before {
    left: 13%;
}
.section-title-sub::after {
    right: -10%;
}
.section-title-sub__ja {
    font-size: 36px;
    text-align: center;
    @include mq(md) {
        font-size: 18px;
    }
    @include mq(ss) {
        font-size: 16px;
    }
}
.section-title-sub__en {
    font-size: rem(14);
    // padding-left: rem(76);
    text-align: center;
    font-family: $sub-font-family;
}

.affiliated__title-area.section-title-sub::after {
    @include mq('ll') {
       right: -1%; 
    }
}