@use "global" as *;
.shared-swiper {
    position: relative;
}

.shared-swiper__container {
    max-width: rem(1077);
    width: 100%;
    margin: auto;
    padding: 0 rem(25);
    @include mq(md) {
        width: 100%;
    }
}

.shared-swiper__img {
    max-width: rem(310);
    width: 100%;
    padding-top: 66.2%;
    position: relative;
    @include mq(md) {
        position: relative;
        max-width: 100%;
    }
}
.shared-swiper__img img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.shared-swiper__content {
    margin-top: rem(40);
}

.shared-swiper__title {
    font-size: 16px;
    font-weight: $bold;
    font-family: $yugothic;
    text-align: center;
    @include mq(md) {
        font-size: 14px;
    }
    @include mq(ss) {
        font-size: 12px;
    }
}

.shared-swiper__text {
    font-size: rem(14);
    font-weight: 500;
    font-family: $yugothic;
    margin-top: rem(20);
    @include mq(md) {
        text-align: center;
        font-size: 12px;
    }
}

/*  */
.swiper-pagination {
    bottom: rem(-95) !important;
}
/*  */
.swiper-pagination-bullet {
    background: $white !important;
    opacity: 0.5 !important;
}
.swiper-pagination-bullet-active {
    background: $white !important;
    opacity: 1 !important;
}

/*  */
.swiper-button-prev,
.swiper-button-next {
    height: 50px;
    width: 50px;
}
/*  */
.swiper-button-prev:after,
.swiper-button-next:after {
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
    height: 50px;
    margin: auto;
    width: 50px;
}
/*  */
.swiper-button-prev:after {
    background-image: url(../images/facility/facility-arrow-left.png);
}
/*  */
.swiper-button-next:after {
    background-image: url(../images/facility/facility-arrow-right.png);
}

.swiper-button-prev {
    top: 55% !important;
    left: 0 !important;
    right: auto !important;
    @include mq(md) {
        top: 45% !important;
    }
}
.swiper-button-next {
    top: 55% !important;
    right: 0 !important;
    left: auto !important;
    @include mq(md) {
        top: 45% !important;
    }
}
