@use "global" as *;

.mv {
    padding-top: 58px;
}
.mv__inner {
}
.mv__background {
    display: inline-block;
    margin-top: auto;
    background-color: $gray;
    max-width: 214px;
    height: 636px;
    // max-width: vw(1366, 214);
    width: 100%;
    // padding-top: 46.57%;
    z-index: -1;
    @include mq(md) {
        max-width: rem(214);
    }
}
// relative
.mv__title-adjust {
    // top: vw(1366, 582);
    // left: rem(58);
    // left: fmin(58, 1366);
    top:582px;
    left: 58px;
    position: absolute;
    @include mq(md) {
        // top: vw(765, 450);
        // left: rem(40);
        // left: fmin(40, 765);
        top:582px;
        left: 40px;
    }
}
.mv__title {
    font-size: 63px;
    // font-size: fmin(63, 1366);
    letter-spacing: 0.13em;
    font-family: $sawarabi;
    position: relative;
    @include mq("ll") {
        font-size: 40px;
        // font-size: fmin(60, 1280);
    }
}

.mv__wrapper {
    display: flex;
    position: relative;
}

.mv__image {
    position: relative;
    width: 85%;
    max-width: 1152px;
    height: 814px;
    // padding-top: vw(1366, 814);
    margin-left: auto;
    @include mq(md) {
        max-width: rem(1152);
    }
}

.mv__image img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
