@use "global" as *;
.facility__title-ja.mv-sub__title-ja {
    @include mq(md) {
        font-size: 17px;
    }
    @include mq(ss) {
        font-size: 15px;
    }
}

.facility__title-ja {
	font-family: $yumincho;
}

.facility__border {
	margin-top: 16%;
	max-width: 1076px;
	max-width: 67.25rem;
	width: 100%;
}

.facility__button-area {
	text-align: center;
	margin-top: 160px;
	margin-top: 10rem;
}

.facility__button {
	max-width: 937px;
	max-width: 58.5625rem;
	width: 100%;
	background-color: #e9e9e9;
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	margin: auto;
}

.facility__button img {
	max-width: 271px;
	max-width: 16.9375rem;
	width: 100%;
}

.facility__button span::after {
	content: "";
	width: 20px;
	width: 1.25rem;
	height: 20px;
	height: 1.25rem;
	border: 0;
	border-top: solid 2px #000;
	border-right: solid 2px #000;
	position: absolute;
	top: 47%;
	right: 15%;
	margin-top: -4px;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	color: #000;
	@include mq('sm') {
		right: 7%;
	}
}

.facility__button span {
	text-align: center;
	font-size: 40px;
	font-size: 2.5rem;
	font-family: $sawarabi;
	color: #000;
	margin-left: 37px;
	margin-left: 2.3125rem;
	margin-right: 60px;
	@include mq('mm') {
		font-size: rem(32);
	}
	@include mq('ss') {
		font-size: 12px;
	}
}

.facility__gallery {
	margin-top: rem(430);
}
